<template>
<div>
    <button class="btn" @click="start()">开始录音1.111</button>
    <button class="btn" @click="stop()">结束录音2</button>
</div>
</template>
<script>
export default {
    name: 'luyin',
    data() {

    },
    methods: {
        start() {
            console.log(1);
            window.dsBridge.call('testAsyn', 'startRecord', res => {
                console.log('开始录音了')
                console.log(res);
            })
        },
        stop() {
            let _this = this;
            console.log(12);
            window.dsBridge.call('testAsyn', 'stopRecord', res => {
                console.log('结束录音了')
                console.log();
                if(typeof(res) == 'string') {
                    res = JSON.parse(res);
                    let blob = _this.dataURItoBlob(res.url)
                    console.log(blob);
                } else {
                    let blob = _this.dataURItoBlob(res.url);
                    console.log(blob);
                }
            })
        },
        dataURItoBlob(base64Data) {
            var byteString;
            if(base64Data.split(',')[0].indexOf('base64') >= 0)
                byteString = atob(base64Data.split(',')[1]);
            else{
                byteString = unescape(base64Data.split(',')[1]);
            }
            var mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
            var ia = new Uint8Array(byteString.length);
            for(var i = 0; i < byteString.length; i++) {
                ia[i] = byteString.charCodeAt(i);
            }
            var blob = new Blob([ia], {
                type: mimeString
            });
            return blob;
        },
        // down(){
        //     const objectUrl = URL.createObjectURL(blob);
        //     const a = document.createElement('a');
        //     a.setAttribute('style', 'display:none');
        //     a.setAttribute('href', objectUrl);
        //     a.setAttribute('download', fileName);
        //     a.click();
        //     URL.revokeObjectURL(objectUrl);
        // }
    }
}
</script>
